import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment as Constants } from "src/environments/environment";

@Injectable({
  providedIn: 'root'
})
export class CalculationService {
  constructor(private http: HttpClient) { }

  SaveCalculation(data){
    return this.http.post(Constants.crudCalculations,data);
  }
  UpdateCalculation(data){
    return this.http.patch(Constants.crudCalculations,data);
  }
  getCalculationsList(){
    return this.http.get(Constants.GetCalculations);
  }
  getCalculation(Id){
    return this.http.get(Constants.crudCalculations+Id);
  }
  deleteCalculation(Id){
    return this.http.delete(Constants.crudCalculations+Id);
  }
}
