import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CalculationComponent } from './calculation.component';
import { CalculationListComponent } from './components/calculation-list/calculation-list.component';
import { CalculationWriteComponent } from './components/calculation-write/calculation-write.component';

const routes: Routes = [
  {
    path: "",
    component: CalculationComponent,
    children: [
      {
        path: '',
        component: CalculationListComponent,
      },
      {
        path:'add',
        component: CalculationWriteComponent,
      },{
        path:'add/:Id',
        component: CalculationWriteComponent,
      },
    ],
  },
    
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CalculationRoutingModule {}
