import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators ,FormControl, } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { CalculationService } from '../../services/calculation.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Calculation } from '../../models/calculation';

@Component({
  selector: 'app-calculation-write',
  templateUrl: './calculation-write.component.html',
  styleUrls: ['./calculation-write.component.scss']
})
export class CalculationWriteComponent implements OnInit {
   
  forms:FormGroup;
  Variables;
  Id:number;

  constructor(
    private service:CalculationService,
    private spinner:NgxSpinnerService,
    private toastr:ToastrService,
    private fb:FormBuilder,
    private router:Router,
    private route:ActivatedRoute,
  ) { }

  ngOnInit(): void {
    this.forms = this.fb.group({
        Id: [''],
        Name :['',Validators.required],
        Script :['',Validators.required],
    });
    this.Id = this.route.snapshot.params['Id'];
    if(this.Id){
      this.getCalculation(this.Id);
    }
    this.getVariables();
  }
  
  getCalculation(Id){
    this.spinner.show();
    this.service.getCalculation(Id).subscribe( (res:Calculation) => {
      this.spinner.hide();
      this.forms.setValue({
        Id:res.Id,
        Name:res.Name,
        Script:res.Script,
      });
    });
  }

  getVariables(){
    this.Variables = [
      { Name:"Description",Value: "Description" },
      { Name:"Description1",Value: "Description1" },
    ];
  }

  SaveCalculation(){
    this.spinner.show();
    if(this.Id){
      this.service.UpdateCalculation(this.forms.value).subscribe(res=>{
        this.toastr.success('Calculation updated');
        this.spinner.hide();
        this.router.navigate(['calculation']);
      },
      err=>{
        this.spinner.hide();
        this.toastr.error(err.error.Message);
      });
    }else{
      this.service.SaveCalculation(this.forms.value).subscribe(res=>{
        this.toastr.success('Calculation added');
        this.spinner.hide();
        this.router.navigate(['calculation']);
      },
      err=>{
        this.spinner.hide();
        this.toastr.error(err.error.Message);
      });
    }
  }

  InsetVariable(Variable){
    this.forms.controls['Script'].setValue(this.forms.value.Script +' '+Variable.Name);
  }

}
