import { Component, OnInit } from '@angular/core';
import { CalculationService } from '../../services/calculation.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-calculation-list',
  templateUrl: './calculation-list.component.html',
  styleUrls: ['./calculation-list.component.scss']
})
export class CalculationListComponent implements OnInit {

  currentPage = 1;
  itemsPerPage = 10;
  Calculations:any = [];

  constructor(
    private service:CalculationService,
    private spinner:NgxSpinnerService,
    private toastr:ToastrService

  ) { }

  ngOnInit(): void {
    this.getCalculationsList();
  }
  getCalculationsList(){
    this.spinner.show();
    this.service.getCalculationsList().subscribe(res => {
      this.Calculations = res;
      this.spinner.hide();
    },err=>{
      this.spinner.hide();
      this.toastr.error(err.error.Message);
    });
  }
  deleteCalculation(Calculation){
    this.spinner.show();
    this.service.deleteCalculation(Calculation.Id).subscribe(res =>{
      this.toastr.success('Calculation Deleted');
      this.spinner.hide();
      this.getCalculationsList();
    },err=>{
      this.spinner.hide();
      this.toastr.error(err.error.Message);
    });
  }
}
